import React, { useEffect, useState } from 'react';
import styles from './ProgressBar.module.scss';
import cx from 'classnames';

let timerToLoading = null;
let timerToIdle = null;

const State = {
    IDLE: 'IDLE',
    LOADING: 'LOADING',
    COMPLETED: 'COMPLETED'
}

const TIME_TO_LOADING = 200
const TIME_TO_IDLE = 100

function ProgressBar() {
    const [ currentState, setCurrentState ] = useState(State.IDLE)
    const [ onGoingCall, setOnGoingCall ] = useState(0)

    useEffect(() => {

        // fast call no progress
        // setTimeout(() => {
        //     startLoad()
        //     setTimeout(() => {
        //         endLoad()
        //     }, 100 )
        // }, 100);

        // slow call 
        setTimeout(() => {
            startLoad()
            setTimeout(() => {
                endLoad()
                setTimeout(() => {
                    startLoad()
                    setTimeout(() => {
                        endLoad()
                    }, 400)
                }, 300);
            }, 600)
        }, 100);

    }, [])

    function setState(state){
        setCurrentState(state)
    }

    const startLoad = () => {
        switch(currentState){
            case State.COMPLETED:
                clearTimeout(timerToIdle)
                timerToIdle = null
                setState(State.IDLE)
                break
            case State.IDLE:
                if(timerToLoading === null){
                    timerToLoading = setTimeout(() => {
                        setState(State.LOADING)
                        timerToLoading = null
                    },TIME_TO_LOADING);
                }
                setOnGoingCall(onGoingCall + 1)
                break
            case State.LOADING:
                setOnGoingCall(onGoingCall + 1)
                break
            default: return null
        }
    }
    
    const endLoad = () => {
        setOnGoingCall(Math.min(0, onGoingCall - 1))
        switch(currentState){
            case State.IDLE:
                clearTimeout(timerToLoading)
                timerToLoading = null
                break
            case State.LOADING:
                if(onGoingCall === 0){
                    setState(State.COMPLETED)
                }
                break
            case State.COMPLETED:
                break
            default: return null
        }
    }

    const handleTransitionEnd = (e) => {
        e.persist(); 
        console.log(e)

        if(currentState === State.COMPLETED){
            timerToIdle = setTimeout( () => setState(State.IDLE), TIME_TO_IDLE )
        }
    }

    return (
        <div className={styles.container}>
            <div className={styles.progressDiv} onTransitionEnd={handleTransitionEnd}>
                <div className={cx( styles.progressBar,
                    { [styles.IDLE] : currentState === 'IDLE' },
                    { [styles.LOADING] : currentState === 'LOADING' },
                    { [styles.COMPLETED] : currentState === 'COMPLETED' }
                )}></div>
            </div>
        </div>
    )
}

export default ProgressBar
