import React, { useState, useEffect, useRef } from 'react'
import {v4} from 'uuid';
import cx from 'classnames';
import faker from 'faker'

import styles from './styles.module.scss'
import ChervonDown from '../shared/icons/ChervonDown'
// import { MSG_BODY } from '../constants';

const data = [
    {
        id: v4(),
        title: faker.lorem.words(5),
        body: faker.lorem.sentences(10)
    },
    {
        id: v4(),
        title: faker.lorem.words(4),
        body: faker.lorem.sentences(3)
    },
    {
        id: v4(),
        title: faker.lorem.words(5),
        body: faker.lorem.sentences(15)
    },
    {
        id: v4(),
        title: faker.lorem.words(2),
        body: faker.lorem.sentences(12)
    }
]

function Accordion() {
    const [active, setActive] =useState([])

    const accordionRef = useRef()

    useEffect( () => {
        document.addEventListener('click', handleOutSideClick);
        return () => {
            document.removeEventListener('click', handleOutSideClick)
        }
    })

    const handleOutSideClick = (e) => {
        if(!accordionRef?.current?.contains(e.target)){
            setActive([])
        }
    }

    const handleOnClick = (id) => {
        if(active.includes(id)){
            let newArray = active.filter( item => item !== id)
            setActive(newArray)
        } else {
            setActive([...active, id])
        }
    }
    return (
        <div className={styles.accordion} ref={accordionRef}>
            {
                data.map((item, index) => {
                    return (
                        <div key={item.id} className={styles.accordionItem}>
                            <button 
                                className={cx(styles.accordionHeader, { [styles.headerActive] : active.includes(item.id) })} 
                                onClick={() => handleOnClick(item.id)}
                            >
                                <strong>{item.title}</strong>
                                <ChervonDown 
                                    width={20} 
                                    height={10} 
                                    className={cx(styles.icon, { [styles.iconActive] : active.includes(item.id)})}/>
                            </button>
                            <div 
                                className={cx(styles.accordionBody,
                                    { [styles.activeBody] : active.includes(item.id)}) }
                            >{ item.body }</div>
                        </div>
                    )
                })
            }
        </div>
    )
}

export default Accordion
