import React, { useEffect, useState, useRef } from 'react';
import faker from 'faker'
// import ReactPaginate from 'react-paginate';
// import InfiniteScroll from 'react-infinite-scroll-component';
// import { List } from "react-virtualized";
// import { FixedSizeList as List } from "react-window";
import './styles.css';


const data = new Array(100).fill().map((value, index) => (
    {
        id: index,
        title: faker.lorem.words(5),
        body: faker.lorem.sentences(2)
    })
)

const ITEM_HEIGHT = 75
const TOTAL_ITEM = data.length;
const WINDOW_HEIGHT = 600;

function RenderList() {
    const [scrollTop, setScrollTop] = useState(0)
    const innerHeight = TOTAL_ITEM * ITEM_HEIGHT;
    const startIndex = Math.floor( scrollTop / ITEM_HEIGHT )
    const endIndex = Math.min(TOTAL_ITEM - 1, Math.floor( (scrollTop + WINDOW_HEIGHT) / ITEM_HEIGHT ))

    const handleOnScroll = (e) => {
        console.log(">>>>>> #### >>>>> : ",e.currentTarget.scrollTop)
        setScrollTop(e.currentTarget.scrollTop)
    };
    console.log({scrollTop, startIndex, endIndex})
    return (
        <div className="scroll" style={{overflowY: "scroll", height: `${WINDOW_HEIGHT}px`}} onScroll={handleOnScroll}>
            <div className="inner" style={{ position: "relative", height: `${innerHeight}px`, border: "1px solid blue" }}>
                {
                    data.slice(startIndex, endIndex).map( (item, index) => {
                        return (
                            <div 
                                className="item-css" 
                                key={item.id} 
                                style={{
                                    position: "absolute",
                                    top: `${(startIndex + index) * ITEM_HEIGHT}px`,
                                    width: "100%",
                                    height: `${ITEM_HEIGHT}px`,
                                    padding: "5px",
                                    border: "1px solid",
                                    margin: "5px"
                                    }}
                            >
                                <h4>{item.title + "     " + item.id}</h4>
                                <p>{item.body}</p>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    )
}


export default RenderList;

